.watch_item {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    position: relative;
    max-height: 100px;
    padding: 16px;
    line-height: 1.1;
    border-radius: 2px;
    -webkit-box-shadow: 0px 1px 5px 0px rgba(0, 0, 0, 0.2),
      0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 3px 1px -2px rgba(0, 0, 0, 0.12);
            box-shadow: 0px 1px 5px 0px rgba(0, 0, 0, 0.2),
      0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 3px 1px -2px rgba(0, 0, 0, 0.12);
}

.wrap_img {
    width: 35%;
}

.wrap_img img{
    width: 70px;
}

.wrap_content {
    position: relative;
    width: 50%;
}

.wrap_content p {
    font-size: 12px;
    font-weight: bold;
    margin: 0px 0 10px 0px;
}

.icon {
    width: 12px;
    height: 12px;
    fill: #bdbdbd;
}

.wrap_button {
    position: absolute;
    right: -45px;
    bottom: -10px;
} 

.btn_del,
.btn_info {
  outline: none;
  cursor: pointer;
  border: none;
  background-color: #fff;
  width: 25px;
  height: 25px;
  border-radius: 50%;
  -webkit-box-shadow: 0 1px 5px rgba(0, 0, 0, 0.25), 0 1px 2px rgba(0, 0, 0, 0.22);
          box-shadow: 0 1px 5px rgba(0, 0, 0, 0.25), 0 1px 2px rgba(0, 0, 0, 0.22);
  padding: 7px;
}

.btn_del {
  margin-right: 5px;
}

.btn_info:hover {
  background-color: #f5f5f5;
}

.btn_del:hover {
  background-color: #f5f5f5;
}

.del_button:hover .icon {
  fill: black;
  -webkit-transition: .5s linear;
  -o-transition: .5s linear;
  transition: .5s linear;
}

.info_button:hover .icon {
  fill: black;
  -webkit-transition: .5s linear;
  -o-transition: .5s linear;
  transition: .5s linear;
}



