.movie {
    height: 300px;
    width: 300px;
    color: white;
    background-color: #000;
}
.modal {
    min-height: 470px;
    width: 800px;
    padding: 10px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    background-color: #fff;
    /* animation: open-modal 300ms ease-in; */
  }
  .overlay {
    background-color: rgba(0, 0, 0, 0.5);
  }
  
  .poster {
    width: 300px;
    height: auto;
    margin-right: 15px;
  }
  .content {
    display: -ms-flexbox;
    display: flex;
  }
  .original_title {
    font-size: 26px;
    margin-bottom: 5px;
  }
  .tagline {
    margin-bottom: 10px;
    font-style: italic;
    font-size: 16px;
  }
  .overview {
    font-size: 12px;
    margin-bottom: 5px;
  }
  .headers {
    margin-bottom: 5px;
    color: #37474f;
  }
  .list {
    margin-bottom: 14px;
    padding-left: 15px;
    line-height: 1.5;
  }
  .list:last-child {
    margin-bottom: 0;
  }
  .genre,
  .company {
    display: inline-block;
    margin-right: 15px;
    color: #01579b;
    font-size: 13px;
    font-weight: bold;
  }
